@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;0,900;1,700&family=Poppins:wght@300;500;600;700&display=swap');


*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root{
    --color-bg: rgba(3, 3, 78, 0.884);
    --color-bg-variant: rgba(128, 0, 128, 0.705);
    --color-bg-primary: rgba(34, 1, 34, 0.904);
    --color-primary: rgba(204, 214, 218, 0.644);
    --color-white: white;
    --color-light: rgba(13, 26, 201, 0.397);

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
    --transtion: all 0.5s ease 

}

html{
    scroll-behavior: smooth;
}

body{
    font-family: 'Poppins', sans-serif;
    background-color: var(--color-bg);
    color: white;
    line-height: 1.7;
   
}

/* ------------- GENENAL STYLE --------------------------- */

.container{
    width: var(--container-width-lg);
    margin: 0 auto;
}


h1,
h2,
h3,
h4,
h5{
    font-weight: 500;
}
h1{
    font-size: 2.5rem;
}

section{
    margin-top: 8rem;
    /* height: 100vh; */

}

section > h2, section > h5{
    text-align: center;
    color: var(--color-white);
}
section > h2{
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light{
    color: var(--color-primary);
}

a{
    color: var(--color-primary);
    transition: all 400ms ease;
}
a:hover{
    color: var(--color-white);

}

.btn{
    width: max-content;
    display: inline-block;
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transtion);    
}

.btn:hover{
    background-color: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;

}

.btn-primary{
    background: var(--color-primary);
    color: var(--color-bg);
   

}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}

/* -------------- medium devices(medium devices) -------------------------- */

@media screen and (max-width: 1024px) {
    .container{
        width: var(--container-width-md);

    }
    section {
        margin-top: 6rem;
    }



}
/* -------------- medium devices(small devices) -------------------------- */

@media screen and (max-width: 600px) {
    .container{
        width: var(--container-width-sm);

    }
    section > h2 {

        margin-bottom: 2rem;

    }



}